select {
  appearance: none;
  outline: none;
  border: none;
  background-color: transparent;
}

.nice-select {
  width: 100%;
  height: auto;
  font-size: inherit;
  position: relative;
}
.nice-select.open::before {
  content: "\f077";
  font-family: var(--fontawesome-pro);
  transition: var(--transition);
}
.nice-select ul.list {
  width: 100%;
}
.nice-select ul.list li.selected {
  font-weight: 500;
}
.nice-select:focus {
  border-color: transparent;
}
.nice-select::before {
  position: absolute;
  content: "\f078";
  font-family: var(--fontawesome-pro);
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
  color: var(--rts-heading);
  font-size: 12px;
  transition: var(--transition);
}
.nice-select::after {
  border-bottom: 0;
  border-right: 0;
}

.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 30px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--rts-button-1);
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 5px;
  bottom: 5px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

input:checked + .slider {
  background-color: var(--rts-button-1);
}

input:checked + .slider:before {
  transform: translateX(30px);
}

.newsletter {
  position: relative;
  max-width: 310px;
}
@media screen and (max-width: 576px) {
  .newsletter {
    max-width: 100%;
  }
}
.newsletter input {
  position: relative;
  padding: 20px 18px;
  padding-right: 120px;
  border-radius: 10px;
  border: 0;
  width: 100%;
}
.newsletter button {
  position: absolute;
  right: 15px;
  padding: 10px;
  border-radius: 6px;
  font-size: 16px;
}

.single__input {
  position: relative;
}

.checkbox input {
  padding: 0;
  height: initial;
  width: initial;
  margin-bottom: 0;
  display: none;
  cursor: pointer;
}

.checkbox label {
  position: relative;
  cursor: pointer;
  font-size: 16px;
}
.checkbox label::before {
  content: "";
  /* -webkit-appearance: none; */
  background-color: transparent;
  border: 1px solid var(--heading-color);
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05), inset 0px -15px 10px -12px rgba(0, 0, 0, 0.05);
  padding: 6px;
  display: inline-block;
  position: relative;
  vertical-align: middle;
  cursor: pointer;
  border-radius: 6px;
  margin-right: 20px;
  top: -2px;
  height: 20px;
  width: 20px;
}

.checkbox input:checked + label::after {
  content: "";
  display: block;
  position: absolute;
  top: 5px;
  left: 7px;
  width: 6px;
  height: 12px;
  border: solid var(--heading-color);
  border-width: 0 1px 1px 0;
  transform: rotate(45deg);
}

.checkbox input:checked + label::before {
  background-color: var(--rts-button-1);
  border-radius: 6px;
}

textarea::placeholder,
input::placeholder {
  color: var(--rts-para) !important;
}

input:focus {
  outline: none;
}

[data-theme=dark] select {
  color: #999999 !important;
}
[data-theme=dark] textarea::placeholder,
[data-theme=dark] input::placeholder {
  color: #999999 !important;
}

input {
  transition: border-color 0.3s ease;
}

textarea:focus, input:focus {
  outline: 1px solid var(--heading-color);
}

textarea:focus-visible, input:focus-visible {
  outline: 1px solid var(--heading-color);
}

.navigation__menu {
  display: flex;
  justify-content: center;
  width: 100%;
}

.navigation__menu ul {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 24px;    /* default gap */
  padding: 0;
  margin: 0;
  list-style: none;
}

.navigation__menu--item {
  font-size: 16px;
  white-space: nowrap;
}

/* ---------------- Desktop breakpoints ---------------- */

/* large screens 1600px+ */
@media (max-width: 1600px) {
  .navigation__menu ul {
    gap: 20px;
  }
}

/* laptops 14001599px */
@media (max-width: 1400px) {
  .navigation__menu ul {
    gap: 16px;
  }
}

/* smaller laptops 12001399px */
@media (max-width: 1300px) {
  .navigation__menu ul {
    gap: 14px;
  }
}

/* 11001199px */
@media (max-width: 1200px) {
  .navigation__menu ul {
    gap: 10px;
    font-size: 15px;
  }
}

/* 10241099px  minimum desktop range */
@media (max-width: 1100px) {
  .navigation__menu ul {
    gap: 8px;
  }
  .navigation__menu--item {
    font-size: 14px;
  }
}

.navigation__menu--item:last-child {
  margin-right: 0;
}
.navigation__menu--item:last-child::after {
  display: none;
}
.navigation__menu--item:first-child {
  margin-left: 0;
}
.navigation__menu--item:hover > a {
  color: var(--btn-1) !important;
}
.navigation__menu--item__link {
  position: relative;
  font-size: 16px;
  text-transform: capitalize;
  padding-top: 24px;
  padding-bottom: 30px;
  font-weight: 400;
  display: block;
  color: var(--rts-para);
  transition: padding 0.3s ease;
  transition: var(--transition);
}
.navigation__menu--item.has-child:hover ul.submenu {
  opacity: 1;
  visibility: visible;
  transform: scaleY(1);
}
.navigation__menu--item.has-child:hover ul.submenu li.has-child:hover ul {
  opacity: 1;
  visibility: visible;
  transform: scaleY(1);
}
.navigation__menu--item.has-arrow {
  position: relative;
}
.navigation__menu--item.has-arrow::before {
  position: absolute;
  content: "\f122";
  font-family: flaticon_bokinn !important;
  top: 48%;
  right: -15px;
  transform: translateY(-50%);
  color: var(--rts-heading);
  font-size: 14px;
  transition: var(--transition);
}
.navigation__menu--item.has-arrow:hover::before {
  content: "\f121";
  font-family: flaticon_bokinn !important;
  transition: var(--transition);
  color: var(--rts-button-1);
}
.navigation__menu--item ul.submenu,
.navigation__menu--item .sub__style {
  position: absolute;
  top: 100%;
  min-width: 150px;
  background: var(--rts-white);
  padding-bottom: 15px;
  z-index: 1024;
  display: inline-block;
  opacity: 0;
  /* padding: 20px 0 30px 0; */
  transform: scaleY(0);
  transform-origin: top center;
  visibility: hidden;
  transition: var(--transition);
  left: 0;
  box-shadow: 0 36px 35px rgba(0, 0, 0, 0.08);
}
.navigation__menu--item ul.submenu li,
.navigation__menu--item .sub__style li {
  position: relative;
  display: block;
  padding: 0 30px;
}
.navigation__menu--item ul.submenu li:last-child,
.navigation__menu--item .sub__style li:last-child {
  margin-bottom: 0;
}
.navigation__menu--item ul.submenu li:hover > a,
.navigation__menu--item .sub__style li:hover > a {
  color: var(--heading-color);
  transition: var(--transition);
}
.navigation__menu--item ul.submenu li:hover > a::before,
.navigation__menu--item .sub__style li:hover > a::before {
  width: 80%;
}
.navigation__menu--item ul.submenu li:not(:last-child),
.navigation__menu--item .sub__style li:not(:last-child) {
  margin: 0 0 10px;
}
.navigation__menu--item ul.submenu li.has-arrow,
.navigation__menu--item .sub__style li.has-arrow {
  position: relative;
}
.navigation__menu--item ul.submenu li.has-arrow::before,
.navigation__menu--item .sub__style li.has-arrow::before {
  position: absolute;
  content: "\f121";
  font-family: flaticon_bokinn !important;
  top: 50%;
  right: 25px;
  transform: translateY(-50%);
  color: var(--rts-heading);
  font-size: 14px;
  transition: var(--transition);
}
.navigation__menu--item ul.submenu li.has-arrow:hover::before,
.navigation__menu--item .sub__style li.has-arrow:hover::before {
  content: "\f122";
  font-family: flaticon_bokinn !important;
  transition: var(--transition);
}
.navigation__menu--item ul.submenu li a,
.navigation__menu--item .sub__style li a {
  padding: 0;
  font-size: 15px;
  text-transform: capitalize;
  font-weight: 400;
  position: relative;
  color: var(--rts-heading);
}
.navigation__menu--item ul.submenu li a::before,
.navigation__menu--item .sub__style li a::before {
  left: 0;
  bottom: 0;
  width: 0;
  height: 1px;
  background: var(--heading-color);
  transition: var(--transition);
}
.navigation__menu--item ul.submenu li a:hover::before,
.navigation__menu--item .sub__style li a:hover::before {
  width: 100%;
}
.navigation__menu--item ul.submenu li ul,
.navigation__menu--item .sub__style li ul {
  top: 0;
  position: absolute;
  left: 100%;
  visibility: hidden;
  transform: scaleY(0);
  transform-origin: top center;
  opacity: 0;
  transition: var(--transition);
}

.header__sticky {
  position: relative;
  top: 0;
  display: block;
  width: 100%;
  box-shadow: none;
  background: transparent;
  z-index: 999;
  margin: auto;
  align-items: center;
  left: 0;
  border-radius: 0;
  transition: all 0.5s ease-in-out;
  top: 0;
  transform: translateY(0);
}
.header__sticky.fixed {
  position: fixed !important;
  box-shadow: 0px 7px 18px rgba(24, 16, 16, 0.0509803922);
  background: var(--rts-white);
}

.main__header.is__home__three .navigation .navigation__menu--item.has-arrow::before {
  color: var(--rts-white);
}
.main__header.is__home__three .navigation .navigation__menu--item.has-arrow:hover::before {
  color: var(--btn-1);
}
.main__header.is__home__three .navigation .navigation__menu--item__link {
  color: var(--rts-white);
}

[data-theme=dark] .navigation__menu--item.has-arrow::before {
  color: var(--rts-para);
}

.navigation__menu--item.has-child .has__mega__menu {
  position: absolute;
  width: 1000px;
  background-color: var(--rts-white);
  padding: 0px;
  opacity: 0;
  visibility: hidden;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  transition: var(--transition);
  z-index: 1024;
  top: 100%;
